/* SEARCH OUTPUT SECTION */
.search__results {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
}

.search__results--heading {
  text-align: center;
  font-size: 25px;
  color: #fff;
}

.output {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.template__wrapper {
  width: 280px;
  margin: 40px 3px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search__movie--poster {
  box-shadow: 5px 8px 10px rgba(0, 0, 0, 0.7);
  height: 390px;
  width: 90%;
  cursor: pointer;
  border-radius: 2px;
}

.search__movie--poster:hover {
  transition: all 0.3s ease;
  opacity: 0.4;
}

.search__movie--title {
  padding: 10px;
  color: rgba(255, 255, 255, 0.877);
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
}

.more__details {
  margin-top: 10px;
  padding: 8px 10px;
  cursor: pointer;
  background: rgba(252, 7, 7, 0.797);
  color: #fff;
  border: none;
  border-radius: 4px;
}
